<template>
    <div ref="modelContainer" class="model-container"></div>
</template>

<script>
import * as THREE from 'three';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader.js';

export default {
    name: 'ThreeDModel',
    mounted() {
        this.initThreeJS();
    },
    methods: {
        initThreeJS() {
            const scene = new THREE.Scene();
            const camera = new THREE.PerspectiveCamera(75, this.$refs.modelContainer.offsetWidth / this.$refs.modelContainer.offsetHeight, 1, 1000);
            const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true, });
            renderer.setClearColor(0x000000, 0);
            renderer.setSize(this.$refs.modelContainer.offsetWidth, this.$refs.modelContainer.offsetHeight);
            this.$refs.modelContainer.appendChild(renderer.domElement);

            // Lighting
            const ambientLight = new THREE.AmbientLight(0xFFBF00, 0.2);
            scene.add(ambientLight);
            const directionalLight = new THREE.DirectionalLight(0xffffff, 0.8);
            directionalLight.position.set(1, 1, 1).normalize();
            scene.add(directionalLight);

            // Load OBJ
            const loader = new OBJLoader();
            loader.load(
                '/assets/heykel.obj', // Ensure the path is correct
                (object) => {
                    object.scale.set(1, 1, 1);
                    scene.add(object);
                    object.position.y = -100; // Adjust position as needed
                },
                (xhr) => {
                    console.log((xhr.loaded / xhr.total) * 100 + '% loaded');
                },
                (error) => {
                    console.log('An error happened', error);
                }
            );

            camera.position.z = 200;

            // Animation loop for rotation
            const animate = () => {
                requestAnimationFrame(animate);

                if (scene.children.length > 2) { // Assumes the loaded model is the first object
                    scene.children[2].rotation.y += 0.01; // Adjust rotation speed as needed
                }

                renderer.render(scene, camera);
            };

            animate();
        }
    }
}
</script>

<style scoped>
div {
   
    width: 100%;
    /* Adjust based on your layout needs */
    height: 600px;
    /* Adjust as needed */
}

.model-container {
    width: 30%;
    /* Adjust based on your layout needs */
    height: 600px;
    /* Adjust as needed */
    background: transparent;
    /* Make background transparent */
}
</style>